import React, { useEffect, useState } from "react";
import Placeholder from "./cryptocurrency.png";
import axios from "axios";

const News = (props) => {
  const [news, setNews] = useState([]);

  useEffect(async () => {
    const proxyUrl = "https://cors-anywhere.herokuapp.com/";
    const response = await axios.get(
      `${proxyUrl}https://newsdata.io/api/1/news?apikey=pub_10641ef12fac3db0c06611e0f5de105b3a95&language=en&q=bitcoin`
    );
    setNews(response.data.results);
  }, []);
  const currencyHandler = async (k) => {
    const proxyUrl = "https://cors-anywhere.herokuapp.com/";
    const response = await axios.get(
      `${proxyUrl}https://newsdata.io/api/1/news?apikey=pub_10641ef12fac3db0c06611e0f5de105b3a95&language=en&q=${k}`
    );
    setNews(response.data.results);
  };
  return (
    <div>
      <div className="d-flex justify-content-between mt-4 mr-3 mb-4">
        <div className="chip" onClick={() => currencyHandler("bitcoin")}>
          Bitcoin
        </div>
        <div className="chip" onClick={() => currencyHandler("ethereum")}>
          Ethereum
        </div>
        <div className="chip" onClick={() => currencyHandler("cardano")}>
          Cardano
        </div>
      </div>
      <div className="d-flex justify-content-between mr-3 mb-4">
        <div className="chip" onClick={() => currencyHandler("ripple")}>
          Ripple
        </div>
        <div className="chip" onClick={() => currencyHandler("dogecoin")}>
          Dogecoin
        </div>
        <div className="chip" onClick={() => currencyHandler("polkadot")}>
          Polkadot
        </div>
      </div>
      <div className="mt-5 pt-3">
        {news.map((item) => (
          <a
            className="card py-2"
            style={{ cursor: "pointer" }}
            href={item.link}
            target="_blank"
          >
            <div className="row">
              <div className="col-8">
                <h6>{item.title}</h6>
                <p className="text-muted">
                  {item.description && item.description.length > 100
                    ? item.description.slice(0, 98) + "..."
                    : item.description}
                </p>
              </div>
              <div className="col-4">
                <div className="d-flex justify-content-end">
                  <img
                    src={item.image_url ? item.image_url : Placeholder}
                    width="120px"
                    height="100px"
                    style={{
                      borderRadius: "10px",
                      marginTop: "20px",
                    }}
                  />
                </div>
              </div>
            </div>
          </a>
        ))}
      </div>
    </div>
  );
};

export default News;
